import "semantic-ui-css/semantic.min.css";
import "../style.css";
import AppLayout from "../components/layout/AppLayout";
import { withRouter } from "next/router";
import { Grid, Header, Menu, Icon } from "semantic-ui-react";
import fetch from "isomorphic-unfetch";
import VerticalMenu from "../components/navigation/VerticalMenu";

const Index = withRouter(props => (
  <AppLayout>
    <Grid stackable columns={2}>
      <Grid.Column width={4}>
        <Menu vertical borderless>
          <Menu.Item>
            <Menu.Header>Categories</Menu.Header>
            <Menu.Menu>
              {props.hubs.map(hub => (
                <Menu.Item name="coucou" />
              ))}
            </Menu.Menu>
          </Menu.Item>
        </Menu>
      </Grid.Column>
      <Grid.Column width={12}>
        <div style={{ marginLeft: "10px" }}>
          <Menu borderless>
            <Menu.Menu position="right">
              <Menu.Item>
                <Icon name="list" />
              </Menu.Item>
              <Menu.Item>
                <Icon name="grid layout" />
              </Menu.Item>
            </Menu.Menu>
          </Menu>
          <Grid columns={3} stackable />
        </div>
      </Grid.Column>
    </Grid>
  </AppLayout>
));

Index.getInitialProps = async function(context) {
  const res = await fetch("http://localhost:3000/api/hubs");
  const data = await res.json();
  const { hubs } = data;
  console.log(hubs);
  return {
    hubs: [
      {
        _id: "5be99539dbd44b2490525837",
        id: 1,
        name: "front",
        displayName: "Front End Development",
        slug: "front-end-development"
      }
    ]
  };
};

export default Index;
